*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/*Navbar*/
.navbar{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0px 8px 6px -6px gray;
  --webkit-box-shadow : 0px 8px 6px -6px gray;
  border-bottom: 0px solid gray !important;
}

.nav-link{
  font-weight: 300 !important;
}

.active{
  border-bottom: 2px solid black;
}

.navbar-brand{
  font-weight: 700;
  letter-spacing: 3px;
}

/*footer*/
.footer{
  color: white;
  padding: 25px;
  background: rgba(0, 0, 0, 0.84);
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover{
  color: rgba(223, 218, 170, 0.9);
  border-bottom: 1px solid rgba(223, 218, 170, 0.9);
}

/*page not found*/
.pnf{
  display: flex;
  align-items: center;
  min-height: 65vh;
  flex-direction: column;
  justify-content: center;
}
.pnfbtn{
    
    border: 1px solid black;
    color: black;
    text-decoration: none;
    padding: 10px;
    margin-top: 20px;
   
    
}
.pnfbtn:hover{
  color: white;
  background-color: black;
}
.pnfheading{
  font-weight: normal;
}

/*contact us*/
.contactus{
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  justify-content: center;
  max-height: 65vh;
}

/*Policy*/
.policy{
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.textofpolicy{
  padding-top: 100px;
}
.aboutimg{
  padding-top: 90px;
}
.abttxt{
  padding: 0px;
  padding-top: 15px;
}
.plctxt{
  padding: 0px;

}

/*Register page*/
.register{
  display: flex;
  align-items: center;
  justify-content: center;
   min-height: 80vh; 
  flex-direction: column;
  padding-top: 40px;
  padding-bottom: 20px;
  background-color: antiquewhite;
  background-image: linear-gradient(0deg,pink,blue);
}
.containerForm{
  background-color: white;
  width: 360px;
  padding: 20px;
  box-shadow: rgb(27, 21, 21) 0px 5px 15px ;
}
.form-btn{
  background-color: black;
  width: 100%;
  color: white;
  border-radius: 0%;
}
.form-btn:hover{
  border: 1px solid black;
}

/*card text*/
.product-link{
  text-decoration: none;
  color: black !important;
}

.sidebar{
    padding: 2px;
}

@media (max-width: 768px) {
  *{
    overflow-x: hidden;
  }
  .extracol{
    display: none;
  }
  .imgsec{
    padding-bottom: 10px;
  }
  .textofpolicy{
    padding-top: 0px;
  }
  .aboutimg{
    padding-top: 0px;
  }
  .abttxt{
    padding: 10px;
    padding-top: 0px;
  }
  .plctxt{
    padding: 10px;
    padding-top: 0px;
  }
  .containerForm{
    width: 320px;
  }
  .sidebar{
    display: none;
  }
}